<template>
  <div class="main-box activity">
    <div class="main">
      <div class="middle">
        <div class="left">
          <live :name="gardenRecord.name" @toMapView="toMapView"></live>
        </div>
        <div class="right">
          <p class="refresh">数据每隔五分钟自动刷新
            <button class="btn1" @click="refresh">
              <i class="iconfont">&#xe667;</i>刷新</button>
          </p>
          <ul class="ranking">
            <li>
              <h3 class="title">
                <em></em>以平均睡眠时长排名</h3>
              <div class="content">
                <bar v-if="averageSleep.xData[0] !== '暂无数据'" :settings="averageSleep"></bar>
                <!-- <bar :settings="attendance"></bar> -->
                <no-data v-else :item="'平均睡眠'" />
              </div>
            </li>
            <li>
              <h3 class="title">
                <em></em>以平均运动强度排名</h3>
              <div class="content">
                <bar v-if="averageExercise.xData[0] !== '暂无数据'" :settings="averageExercise"></bar>
                <no-data v-else :item="'平均运动强度'" />
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="line"></div>
      <div class="bottom">
        <ul class="statistics">
          <li>
            <h3 class="title">
              <em></em>睡眠情况分布</h3>
            <div class="content">
              <pie v-if="sleepQuality.data[0].name !== '暂无数据'" :data="sleepQuality.data" :color="sleepQuality.color" :title="sleepQuality.title" :pieSize="sleepQuality.pieSize"></pie>
              <no-data v-else :item="'睡眠情况'" />
            </div>
          </li>
          <li>
            <h3 class="title">
              <em></em>运动情况分布</h3>
            <ul class="content">
              <pie v-if="sleepQuality.data[0].name !== '暂无数据'" :data="sportQuality.data" :color="sportQuality.color" :title="sportQuality.title" :pieSize="sportQuality.pieSize"></pie>
              <no-data v-else :item="'运动情况'" />
            </ul>
          </li>
          <li>
            <h3 class="title">
              <em></em>幼儿园信息</h3>
            <div class="content">
              <matts :data="KindergartenData"></matts>
            </div>
          </li>
          <li>
            <h3 class="title">
              <em></em>教师信息</h3>
            <ul class="content">
              <li>
                <pie v-if="jobData.data[0].name !== '暂无数据'" :data="jobData.data" :color="jobData.color" :title="jobData.title" :pieSize="jobData.pieSize"></pie>
                <no-data v-else :item="'职称'" />
              </li>
              <li>
                <pie v-if="ageData.data[0].name !== '暂无数据'" :data="ageData.data" :color="ageData.color" :title="ageData.title" :pieSize="ageData.pieSize"></pie>
                <no-data v-else :item="'年龄'" />
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import ElMap from '@/common/echarts/Map'
import Live from '@/common/ezviz/Live'
import ElLine from '@/common/echarts/Line'
import Bar from '@/common/echarts/Bar'
import Matts from '@/pages/home/healthy/today/components/Matts'
import Pie from '@/common/echarts/Pie'
import http from '@/common/axios/http'
import api from '@/common/axios/api'
export default {
  name: 'HealthyToday',
  components: {
    ElMap,
    ElLine,
    Matts,
    Pie,
    Live,
    Bar
  },
  data () {
    return {
      provinceId: localStorage.getItem('provinceId'),
      cityId: localStorage.getItem('cityId'),
      districtId: localStorage.getItem('districtId'),
      gardenName: localStorage.getItem('gardenName'),
      gardenId: localStorage.getItem('gardenId'),
      mapView: true,
      gardenRecord: {
        name: '',
        location: ''
      },
      // 以平均睡眠时长排名
      averageSleep: {
        xData: [],
        data: [],
        maxVal: 20,
        linearGradient: ['#ff9786', '#ffe98f'],
        hasLine: false,
        yName: '时间（分）',
        grid: {
          top: '20%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      // 以平均运动强度排名
      averageExercise: {
        xData: [],
        data: [],
        maxVal: 20,
        linearGradient: ['#ff9786', '#ffe98f'],
        hasLine: false,
        yName: '动量',
        grid: {
          top: '20%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      // 睡眠情况分布
      sleepQuality: {
        data: [
          { value: 12, name: '基本没睡' },
          { value: 15, name: '睡眠较少' },
          { value: 14, name: '睡眠正常' },
          { value: 16, name: '睡眠充足' }
        ],
        color: ['#00bbf4', '#2dcfea', '#cdffc2', '#2dcfea'],
        title: '睡眠',
        pieSize: {
          radius: '55%',
          center: ['50%', '42%'],
          title: {
            top: 62,
            fontSize: 18
          }
        }
      },
      // 运动情况分布
      sportQuality: {
        data: [
          { value: 12, name: '基本没睡' },
          { value: 15, name: '睡眠较少' },
          { value: 14, name: '睡眠正常' },
          { value: 16, name: '睡眠充足' }
        ],
        color: ['#00bbf4', '#2dcfea', '#cdffc2', '#2dcfea'],
        title: '运动',
        pieSize: {
          radius: '55%',
          center: ['50%', '42%'],
          title: {
            top: 62,
            fontSize: 18
          }
        }
      },
      // 幼儿园信息
      KindergartenData: [
        {
          unit: '家',
          subText: '园所总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '园长总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在职教师总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在园儿童总数',
          sum: 0
        }
      ],
      // 职称分布
      jobData: {
        data: [
          { value: 120, name: '正高级' },
          { value: 200, name: '高级' },
          { value: 260, name: '一级' },
          { value: 200, name: '二级' },
          { value: 80, name: '三级' }
        ],
        color: ['#ffbc00', '#57d0ff', '#4d6bff', '#ff6562', '#9d70ff'],
        title: '职称分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      },
      // 年龄分布
      ageData: {
        data: [
          { value: 225, name: '00后' },
          { value: 210, name: '90后' },
          { value: 222, name: '80后' },
          { value: 125, name: '70后' }
        ],
        color: ['#1683fb', '#61ffe1', '#24cbff', '#7189ff'],
        title: '年龄分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      }
    }
  },
  methods: {
    // 刷新
    refresh () {
      window.$('.refresh').find('i').addClass('loading')
      this.overview()
      this.getGardenInfos()
      this.getAttendRage()
    },
    // 幼儿园，教师信息
    // 幼儿园信息
    getGardenInfos: async function () {
      const res = await http.get(api.getGardenInfos, {
        gardenId: this.gardenId
      })
      let d = res.data.data
      this.KindergartenData[0].sum = d.gardenCount
      this.KindergartenData[1].sum = d.kindergartenCount
      this.KindergartenData[2].sum = d.teacherCount
      this.KindergartenData[3].sum = d.totalCount
      const ages = ['00', '90', '80', '70']
      const jobs = ['正高级', '高级', '一级', '二级', '三级']
      this.jobData.data = [
        { value: 0, name: '正高级' },
        { value: 0, name: '高级' },
        { value: 0, name: '一级' },
        { value: 0, name: '二级' },
        { value: 0, name: '三级' }
      ]
      this.ageData.data = [
        { value: 0, name: '00后' },
        { value: 0, name: '90后' },
        { value: 0, name: '80后' },
        { value: 0, name: '70后' }
      ]
      if (d.jobs.length) {
        for (let i = 0, len = jobs.length; i < len; i++) {
          const result = d.jobs.filter((item) => {
            return item.item1 === jobs[i]
          })
          result.length ? this.jobData.data[i].value = result[0].item2 : this.jobData.data[i].value = 0
        }
      } else {
        this.jobData.data = [{ name: '暂无数据', value: 0 }]
      }
      if (d.ages.length) {
        for (let i = 0, len = ages.length; i < len; i++) {
          const result = d.ages.filter((item) => {
            return item.item1.substring(0, 1) === ages[i].substring(0, 1)
          })
          result.length ? this.ageData.data[i].value = result.length : this.ageData.data[i].value = 0
        }
      } else {
        this.ageData.data = [{ name: '暂无数据', value: 0 }]
      }
    },
    getAttendRage: async function () {
      const result = await http.get(api.getAverageSleepRange, {
        gardenId: this.gardenId
      })
      const res = result.data.data
      // console.log(res)
      // const res = {
      //   gardenSleep: [
      //     {
      //       gradeNum: 1,
      //       averageSleepTime: 1.1
      //     },
      //     {
      //       gradeNum: 2,
      //       averageSleepTime: 3.1
      //     }
      //   ],
      //   gardenSport: [
      //     {
      //       gradeNum: 1,
      //       averageSport: 1.1
      //     },
      //     {
      //       gradeNum: 1,
      //       averageSport: 1.1
      //     }
      //   ]
      // }
      // console.log(res)
      // console.log(res[0].gardenSleep)
      if (res.gardenSleep.length) {
        const resSleep = res.gardenSleep
        for (let i = 0, len = resSleep.length; i < len; i++) {
          if (resSleep[i].gradeNum === 1) {
            resSleep[i].name = '托班'
          } else if (resSleep[i].gradeNum === 2) {
            resSleep[i].name = '小班'
          } else if (resSleep[i].gradeNum === 3) {
            resSleep[i].name = '中班'
          } else if (resSleep[i].gradeNum === 4) {
            resSleep[i].name = '大班'
          }
        }
        const result = resSleep.filter((item) => {
          return (item.name && (item.averageSleepTime !== 0))
        })
        if (result.length) {
          result.sort(function (a, b) {
            return b.averageSleepTime - a.averageSleepTime
          })
          console.log(result)
          let sleepCountName = []
          let sleepCountRate = []
          for (let i = 0, len = result.length; i < len; i++) {
            sleepCountName.push(result[i].name)
            sleepCountRate.push((result[i].averageSleepTime) * 60)
          }
          console.log(sleepCountName, sleepCountRate)
          this.averageSleep.xData = sleepCountName
          this.averageSleep.data = sleepCountRate
          this.averageSleep.maxVal = Math.max(...sleepCountRate) + 20
          console.log(this.averageSleep.xData, this.averageSleep.data)
        } else {
          this.averageSleep.xData = ['暂无数据']
          this.averageSleep.data = [0]
          this.averageSleep.maxVal = 0
        }
      } else {
        this.averageSleep.xData = ['暂无数据']
        this.averageSleep.data = [0]
        this.averageSleep.maxVal = 0
      }
      if (res.gardenSport.length) {
        const resSport = res.gardenSport
        // console.log(resSleep)
        // debugger
        for (let i = 0, len = resSport.length; i < len; i++) {
          if (resSport[i].gradeNum === 1) {
            resSport[i].name = '托班'
          } else if (resSport[i].gradeNum === 2) {
            resSport[i].name = '小班'
          } else if (resSport[i].gradeNum === 3) {
            resSport[i].name = '中班'
          } else if (resSport[i].gradeNum === 4) {
            resSport[i].name = '大班'
          }
        }
        const result = resSport.filter((item) => {
          return (item.name && (item.averageSport !== 0))
        })
        if (result.length) {
          result.sort(function (a, b) {
            return b.averageSport - a.averageSport
          })
          console.log(result)
          let sportCountName = []
          let sportCountRate = []
          for (let i = 0, len = result.length; i < len; i++) {
            sportCountName.push(result[i].name)
            sportCountRate.push(result[i].averageSport)
          }
          console.log(sportCountName, sportCountRate)
          this.averageExercise.xData = sportCountName
          this.averageExercise.data = sportCountRate
          this.averageExercise.maxVal = Math.max(...sportCountRate) + 20
          console.log(this.averageExercise.xData, this.averageExercise.data)
        } else {
          this.averageExercise.xData = ['暂无数据']
          this.averageExercise.data = [0]
          this.averageExercise.maxVal = 0
        }
      } else {
        this.averageExercise.xData = ['暂无数据']
        this.averageExercise.data = [0]
        this.averageExercise.maxVal = 0
      }
    },
    // 当前区域总览数据
    overview: async function () {
      const res = await http.get(api.getSleepQuality, {
        gardenId: this.gardenId
      })
      if (res.data) {
        window.$('.refresh').find('i').removeClass('loading')
        const d = res.data.data
        if (d.length) {
          this.sleepQuality.data = []
          for (let i = 0, len = d.sleep.length; i < len; i++) {
            this.sleepQuality.data.push({
              name: d.sleep[i].item1,
              value: d.sleep[i].item2
            })
          }
          this.sportQuality.data = []
          for (let i = 0, len = d.sport.length; i < len; i++) {
            this.sportQuality.data.push({
              name: d.sport[i].item1,
              value: d.sport[i].item2
            })
          }
        } else {
          this.sleepQuality.data = [{
            name: '暂无数据',
            value: 0
          }]
          this.sportQuality.data = [{
            name: '暂无数据',
            value: 0
          }]
        }
      }
    },
    gardenView (params) {
      this.mapView = !this.mapView
      this.gardenRecord = {
        name: params.name,
        location: params.location
      }
    },
    toMapView () {
      this.mapView = !this.mapView
    }
  },
  mounted () {
    // 初始化所有边框样式
    this.$common.border(window.$('.bottom .content'))
    this.getAttendRage()
    this.overview()
    this.getGardenInfos()
  }
}
</script>

<style lang="stylus" scoped>
.activity
  .statistics
    li:nth-child(1), li:nth-child(2)
      .content
        padding 20px !important
</style>



